<template>
  <div id="app">
    <AmazingLove />
    <LoadingLove />
    <audio src="./assets/music/爱一点.mp3" ref="music" loop="loop"></audio>
    <div class="btn_wrap">
      <el-button ref="playBtn" :type="type" :icon="icon" circle @click="handleMusic"></el-button>
    </div>
  </div>
</template>

<script>
export default {
  name: 'App',
  components: {
    AmazingLove: () => import('@/components/AmazingLove'),
    LoadingLove: () => import('@/components/LoadingLove'),
  },
  data() {
    return {
      type: 'info',
      icon: 'el-icon-bell',
      isPaly: true,
    }
  },
  methods: {
    handleMusic() {
      if (this.isPaly) {
        this.$refs.music.play()
        this.isPaly = !this.isPaly
        this.type = 'primary'
        this.icon = 'el-icon-message-solid'
      } else {
        this.$refs.music.pause()
        this.isPaly = !this.isPaly
        this.type = 'info'
        this.icon = 'el-icon-bell'
      }
    },
  }
}
</script>

<style>
* {
  margin: 0;
  padding: 0;
}

.btn_wrap {
  position: fixed;
  top: 10px;
  left: 5px;
}
</style>
